<template>
  <!-- <div>这是一个编辑资料页面</div> -->
  <div>
    <div>
      <!-- 导航栏 -->
      <van-nav-bar
        class="page-nav-bar"
        left-arrow
        title="个人信息"
        @click-left="$router.push({ path: '/my' })"
      ></van-nav-bar>
      <!-- /导航栏 -->
    </div>
    <div class="celldiv">
      <van-cell center title="头像" is-link>
        <template>
          <van-image round class="userphoto" :src="userinfo.photo" />
        </template>
      </van-cell>
      <!-- 样式：通过定位和透明，覆盖到头像单元格上面 -->
      <input
        type="file"
        ref="userphoto"
        class="userphotoimg"
        @change="optionImg"
      />
      <van-cell
        title="昵称"
        :value="userinfo.name"
        is-link
        @click="editNameShowFn"
      />
      <van-cell
        title="性别"
        :value="userinfo.gender == 0 ? '男' : '女'"
        is-link
        @click="editGenderShowFn"
      />
      <van-cell
        title="生日"
        @click="editBirthdayShowFn"
        :value="userinfo.birthday"
        is-link
      />
    </div>
    <!-- 修改昵称弹层 -->
    <van-popup
      class="vanpopup"
      v-model="editNameShow"
      position="bottom"
      style="height: 100%"
    >
      <van-nav-bar
        title="设置昵称"
        left-text="取消"
        right-text="完成"
        @click-left="editNameShow = false"
        @click-right="editNameFn"
      />
      <div class="popupname">
        <van-field
          v-model="nameMessage"
          rows="2"
          autosize
          type="textarea"
          maxlength="7"
          placeholder="请输入昵称"
          show-word-limit
        />
      </div>
    </van-popup>
    <!-- 编辑性别弹层 -->
    <van-popup class="vanpopup" v-model="editGenderShow" position="bottom">
      <van-picker
        title="性别"
        show-toolbar
        :columns="columns"
        :default-index="userinfo.gender"
        @confirm="editGenderFn"
        @cancel="editGenderShow = false"
      />
    </van-popup>

    <!-- 编辑生日弹层 -->
    <van-popup class="vanpopup" v-model="editBirthdayShow" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title="选择年月日"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="editBirthdayFn"
      />
    </van-popup>

    <!-- 编辑头像弹层 -->
    <van-popup
      class="vanpopup"
      v-model="editPhotoShow"
      position="bottom"
      style="height: 100%"
    >
      <div class="imgbg" v-if="editPhotoShow">
        <!-- 这个img要放我们选择的图片 -->
        <img
          ref="yulanPhotoDom"
          class="imgbgphoto"
          :src="yulanPhotoSrc"
          alt=""
        />
        <div class="toolbar">
          <div class="cancel" @click="editPhotoShow = false">取消</div>
          <div class="confirm" @click="onConfirmPhoto">完成</div>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import moment from "moment";
import "cropperjs/dist/cropper.css";
import Cropper from "cropperjs";
import {
  getUserProfileApi,
  updataUserProfileApi,
  edituserinfoPhotoApi,
} from "@/api/user";
export default {
  data() {
    return {
      editNameShow: false, //编辑姓名弹层状态
      editGenderShow: false, //编辑性别弹层状态
      editBirthdayShow: false, //编辑生日弹层状态
      editPhotoShow: false, //编辑头像弹层状态
      nameMessage: "", //昵称
      userinfo: {}, //用户身份信息
      columns: ["男", "女"],
      currentDate: new Date(), //现在的时间
      minDate: new Date(1970, 0, 1), //最小的时间
      maxDate: new Date(), //最大时间
      yulanPhotoSrc: "", //当前选择的图片
      cropper: "", //cropper实例对象
    };
  },
  async created() {
    try {
      let { data } = await getUserProfileApi();
      console.log(data);
      this.userinfo = data.data;
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
    // 打开昵称
    editNameShowFn() {
      this.editNameShow = true;
      this.nameMessage = this.userinfo.name;
    },
    //打开性别
    editGenderShowFn() {
      this.editGenderShow = true;
    },
    //打开性别
    editBirthdayShowFn() {
      this.editBirthdayShow = true;
    },

    //修改昵称弹层内的完成按钮
    async editNameFn() {
      try {
        this.$toast.loading({
          message: "更新中...",
        });
        let obj = {
          name: this.nameMessage,
        };
        await updataUserProfileApi(obj);
        this.$toast.success("更新成功");
        this.editNameShow = false; //关闭弹层
      } catch (error) {
        this.$toast.fail("昵称更新失败，请重试。。。");
        console.log(error);
      }
    },

    //修改性别弹层内的确认按钮
    // 单列：选中值（item），选中值对应的索引（index）
    async editGenderFn(item, index) {
      try {
        this.$toast.loading({
          message: "更新中...",
        });
        let obj = {
          gender: index,
        };
        await updataUserProfileApi(obj);
        this.$toast.success("更新成功");
        this.editGenderShow = false; //关闭弹层
        //思考：修改完成后，页面性别没变化
        //解决：修改视图，手动修改用户信息对象里面的性别
        this.userinfo.gender = index;
      } catch (error) {
        this.$toast.fail("性别更新失败，请重试。。。");
        console.log(error);
      }
    },
    //修改生日弹层内的确认按钮
    async editBirthdayFn() {
      let date = moment(this.currentDate).format("YYYY-MM-DD");
      try {
        this.$toast.loading({
          message: "更新中...",
        });
        let obj = {
          birthday: date,
        };
        await updataUserProfileApi(obj);
        this.$toast.success("更新成功");
        this.editBirthdayShow = false; //关闭弹层
        //思考：修改完成后，页面性别没变化
        //解决：修改视图，手动修改用户信息对象里面的性别
        this.userinfo.birthday = date;
      } catch (error) {
        this.$toast.fail("生日更新失败，请重试。。。");
        console.log(error);
      }
    },
    //选择
    optionImg() {
      // fils返回的是一个伪数组
      let obj = this.$refs.userphoto.files[0];
      let imgsrc = window.URL.createObjectURL(obj);
      //给图片做预览，设置src属性
      this.yulanPhotoSrc = imgsrc;
      console.log(imgsrc);
      this.editPhotoShow = true;

      // const image = document.getElementById("image");
      //立马获取图片是获取不到的
      //vue里面dom的更新是异步的
      //所以使用$nextTick
      //vue的核心概念，dom的更新是异步的
      this.$nextTick(() => {
        let image = this.$refs.yulanPhotoDom;
        console.log(image);
        this.cropper = new Cropper(image, {
          aspectRatio: 1, //裁剪框的宽高比
          viewMode: 1, //定义cropper的视图模式
          dragMode: "move", //定义cropper的拖拽模式//只可以移动‘crop’: 可以产生一个新的裁剪框3‘move’: 只可以移动3
          // autoCropArea: 1,
          cropBoxMovable: false, //是否通过拖拽来移动剪裁框
          cropBoxResizable: false, //是否通过拖动来调整剪裁框的大小
          background: false, //显示容器的网格背景
        });
      });
    },

    onConfirmPhoto() {
      //getCroppedCanvas返回一个剪裁后的图像
      //toBlob方法创造 Blob 对象，用以展示 canvas 上的图片；这个图片文件可以被缓存或保存到本地。
      this.cropper.getCroppedCanvas().toBlob(async (blob) => {
        //Blob 对象表示一个不可变、原始数据的类文件对象。
        const formData = new FormData();

        // Pass the image file name as the third parameter if necessary.
        formData.append("photo", blob /*, 'example.png' */);
        try {
          this.$toast.loading({
            message: "更新中...",
          });
          let { data } = await edituserinfoPhotoApi(formData);
          this.$toast.success("更新成功");
          //修改完了数据，还要修改视图
          this.userinfo.photo = data.data.photo;
          this.editPhotoShow = false;
        } catch (error) {
          this.$toast.fail("头像修改失败，请重试。。。");
          console.log("更新头像失败" + error);
        }
      });
      console.log("图片点击了完成");
    },
  },
};
</script>
<style lang="less" scoped>
.vanpopup {
  background-color: #f5f7f9;
  .popupname {
    padding: 20px;
  }
}
.userphoto {
  width: 60px;
  height: 60px;
}
.celldiv {
  position: relative;
  .userphotoimg {
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    opacity: 0;
    height: 100px;
  }
}
.imgbg {
  width: 100%;
  height: 100%;
  background: #000;
  .imgbgphoto {
    display: block;
    max-width: 100%;
  }
}
.toolbar {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  .cancel,
  .confirm {
    width: 90px;
    height: 90px;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
  }
}
</style>
